import React, { useState } from "react";
import Top from "../common/Top";
import Wrapper from "../common/Wrapper";
import CategoryButtons from "../common/CategoryButton";
import LoadMenu from "../common/LoadMenu";
import Black from "../img/blackLine.png";
import styled from "styled-components";
import StartButton from "../common/StartButton";
import Menudata from "../data/Menudata";
import { makeStyles } from "@material-ui/core/styles";
import { Route } from "react-router-dom";

const MainWrapper = styled.div`
  text-align: center;
  margin: 0;
  padding: 0;
`;

const Spacer = styled.div`
  height: 1rem;
`;

const Img = styled.img`
  width: 85%;
  overflow: hidden;
  @media (max-width: 359px) {
  }
`;

const Cate1 = styled.div``;

const useStyles = makeStyles({
  text: {
    fontSize: 10,
    marginTop: "15px",
    marginBottom: "60px",
    textAlign: "center",
    fontFamily: "KCC-Ganpan",
    color: "black",
  },
});

function Category() {
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedTypes, setSelectedTypes] = useState([]);
  const [recommendedMenu, setRecommendedMenu] = useState("");

  const handleCategorySelect = (category) => {
    setSelectedCategories((prevCategories) => {
      if (prevCategories.includes(category)) {
        return prevCategories.filter((c) => c !== category);
      } else {
        return [...prevCategories, category];
      }
    });
  };

  const handleTypeSelect = (type) => {
    setSelectedTypes((prevTypes) => {
      if (prevTypes.includes(type)) {
        return prevTypes.filter((t) => t !== type);
      } else {
        return [...prevTypes, type];
      }
    });
  };

  const handleRecommendation = () => {
    if (selectedCategories.length > 0 && selectedTypes.length > 0) {
      const filteredMenu = Object.entries(Menudata).filter(
        ([_, value]) =>
          selectedCategories.includes(value.카테고리) &&
          value.종류.some((type) => selectedTypes.includes(type))
      );

      if (filteredMenu.length > 0) {
        const randomIndex = Math.floor(Math.random() * filteredMenu.length);
        setRecommendedMenu(filteredMenu[randomIndex][0]);
      } else {
        setRecommendedMenu("메뉴를 찾을 수 없습니다.");
      }
    } else {
      setRecommendedMenu("메뉴를 찾을 수 없습니다.");
    }
  };

  return (
    <>
      <Wrapper>
        <MainWrapper>
          <Top />
          <Spacer />
          <div>
            {["한식", "양식", "중식", "일식", "분식", "다른나라"].map(
              (category) => (
                <CategoryButtons
                  key={category}
                  onClick={() => handleCategorySelect(category)}
                  selected={selectedCategories.includes(category)}
                >
                  {category}
                </CategoryButtons>
              )
            )}
          </div>

          <Img src={Black} />

          <div>
            {["밥", "빵", "면", "국물", "고기", "해산물"].map((type) => (
              <CategoryButtons
                key={type}
                onClick={() => handleCategorySelect(type)}
              >
                {type}
              </CategoryButtons>
            ))}
          </div>

          {/*           {selectedCategories.length > 0 && (
            <div>
              <strong>선택한 카테고리:</strong> {selectedCategories.join(", ")}
            </div>
          )}
          {selectedTypes.length > 0 && (
            <div>
              <strong>선택한 타입:</strong> {selectedTypes.join(", ")}
            </div>
          )} */}
          <LoadMenu>{recommendedMenu}</LoadMenu>
          <StartButton onClick={handleRecommendation}>
            메뉴를 추천해줘
          </StartButton>
        </MainWrapper>
      </Wrapper>
      <Spacer />
    </>
  );
}

